"use client";

import React, { Suspense } from "react";
import "./globals.css";
import { Inter } from "next/font/google";
import { useIsMobile } from "../hooks/use-mobile";
import Sidebar from "../components/sidebar";
import Header from "../components/header";
import Loading from "../components/loading";
import { Sheet, SheetContent } from "@/components/ui/sheet";

const inter = Inter({
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap",
});

function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [isOpen, setIsOpen] = React.useState(false);
  const [mounted, setMounted] = React.useState(false);
  const isMobile = useIsMobile();

  React.useEffect(() => {
    setMounted(true);
  }, []);

  // Handle initial loading state
  if (!mounted) {
    return (
      <html lang="es" className={inter.variable}>
        <body className="min-h-screen bg-black text-white">
          <Loading />
        </body>
      </html>
    );
  }

  return (
    <html lang="es" className={inter.variable}>
      <body className="min-h-screen bg-black text-white antialiased">
        <Suspense fallback={<Loading />}>
          {isMobile ? (
            <div className="flex flex-col min-h-screen">
              <Header onMenuClick={() => setIsOpen(true)} />
              <Sheet open={isOpen} onOpenChange={setIsOpen}>
                <SheetContent 
                  side="left" 
                  className="p-0 w-[85%] sm:w-[350px] bg-black border-r border-gray-800"
                >
                  <Sidebar isMobile onClose={() => setIsOpen(false)} />
                </SheetContent>
              </Sheet>
              <main className="flex-1 px-4 pt-20 pb-8 w-full max-w-7xl mx-auto">
                {children}
              </main>
            </div>
          ) : (
            <div className="flex min-h-screen">
              <Sidebar />
              <main className="flex-1 ml-64 p-8 max-w-[1600px]">
                <div className="max-w-7xl mx-auto">
                  {children}
                </div>
              </main>
            </div>
          )}
        </Suspense>
      </body>
    </html>
  );
}

export default RootLayout;
